.footer {
	padding: 31px 0 38px;
	background-color: $bg_secondary;
	.footer__logo, .footer__logo svg {
		width: 187px;
		height: 32px;
	}
	.footer__logo {
		margin: 0 auto 19px;
	}
	.footer__main {
		font-size: 12px;
  	line-height: 20px;
	}
	.footer__text{
		text-align: center;
		margin-bottom: 20px;
	}
	.footer__link {
		text-align: center;
		color: $white;
		margin-bottom: 10px;
		&.bold-text {
			font-family: $SF_Pro_Bold, sans-serif;
			font-size: 16px;
		}
	}

	@include table {
		padding: 61px 0 68px;
		.footer__main {
			font-size: 16px;
			line-height: 24px;
		}
		.footer__links {
			max-width: 503px;
			width: 100%;
			margin: 0 auto;
			@include flex(space-between, flex-start);
		}
		.footer__link {
			margin-bottom: 0;
			&.bold-text {
				font-size: 20px;
			}
		}
	}

	@include desktop-sm {
		.footer__texts {
			max-width: 963px;
			width: 100%;
			margin: 0 auto;
			@include flex(space-between, flex-start);
			margin-bottom: 40px;
		}
		.footer__text {
			margin-bottom: 0;
		}
		.footer__text:first-child {
			width: 365px;
			margin-right: 20px;
		}
		.footer__text:nth-child(2) {
			width: 459px;
		}
	}

	@media screen and (min-width: 1300px) {
		.footer__logo {
			margin: 0 0 19px;
		}
		.footer__main {
			@include flex(space-between);
		}
		.footer__texts {
			margin: 0;
			margin-bottom: 0;
		}
		.footer__text {
			text-align: left;
		}
		.footer__text:nth-child(2) {
			margin-right: 20px;
		}
		.footer__links {
			margin: 0;
		}
	}
}