.why-item {
  padding: 24px 25px 30px;
  background-color: $bg_secondary;
  box-shadow: $boxShadow;
  position: relative;
  cursor: pointer;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $itemGradient;
    opacity: 0;
    transition: $transition;
  }
  &__title {
    margin: 40px 0 21px;
    transition: $transition;
  }
  &:hover {
    .why-item__title {
      color: $orange;
    }
    &::before {
      opacity: 1;
    }
  }

	@include table {
		&__text {
			max-width: 360px;
			width: 100%;
		}
	}

  @include desktop {
		&__title {
      margin: 70px 0 21px;
    }
		padding: 44px 48px 70px;
  }
}
