.download-item {
  padding: 52px 35px 32px;
  background-color: $bg_secondary;
  box-shadow: $boxShadow;
  border-radius: 21px;
  text-align: center;
  position: relative;
  cursor: pointer;
  &__img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    &::before {
      content: '';
      position: absolute;
      top: -13px;
      left: -13px;
      z-index: -1;
      @include size(70px, 70px, 50%);
      background-color: rgba(15, 15, 15, 1);
    }
    &::after {
      content: '';
      position: absolute;
      top: -20px;
      left: -20px;
      z-index: -2;
      @include size(84px, 84px, 50%);
      background-color: $bg_secondary;
    }
    &.key,
    &.key svg {
      width: 40px;
      height: 40px;
    }
    &.key::before {
      top: -15px;
      left: -15px;
    }
    &.key::after {
      top: -22px;
      left: -22px;
    }
  }
  &__title {
    margin-bottom: 12px;
  }

  @include table {
  }

  @include desktop {
    padding: 62px 77px 42px;
  }
}
