#download {
	padding-bottom: 100px;
	.download__btn {
		margin: 0 auto;
	}
	.download__images {
		position: relative;
		text-align: center;
		z-index: -1;
		&::before, &::after {
			content: '';
			position: absolute;
			z-index: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 290px;
			background: $gradient;
		}
		&::before {
			bottom: -63%;
		}
		&::after {
			bottom: -75%;
		}
	}
	.download__image-center {
		@include size(280px, 226px, 12px);
		object-position: top center;
	}
	.download__image-left,
	.download__image-right {
		@include size(150px, 140px, 12px);
		position: absolute;
		top: 20px;
		z-index: 1;
	}
	.download__image-left {
		left: 0;
	}
	.download__image-right {
		right: 0;
	}
	.download__items {
		margin-bottom: 40px;
		margin-top: 10px;
	}
	.download-item:not(:last-child) {
		margin-bottom: 70px;
	}

	@include table-sm {
		.download__image-center {
			@include size(340px, 226px, 12px);
		}
		.download__image-left,
		.download__image-right {
			@include size(210px, 140px, 12px);
			top: auto;
			bottom: 45px;
			z-index: -1;
		}
	}

	@include table {
		padding-bottom: 150px;
    .download__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
    }
		.download-item:not(:last-child) {
			margin-bottom: 0;
		}
		.download__image-center {
			@include size(440px, 226px, 12px);
		}
		.download__image-left,
		.download__image-right {
			@include size(310px, 140px, 12px);
		}
  }

	@include desktop-sm {
		.download__image-center {
			@include size(540px, 276px, 12px);
		}
		.download__image-left,
		.download__image-right {
			@include size(410px, 190px, 12px);
			position: absolute;
			bottom: 20px;
		}
		.download__images {
			&::before, &::after {
				height: 390px;
			}
			&::before {
				bottom: -75%;
			}
			&::after {
				bottom: -100%;
			}
		}
	}

	@include desktop {
		padding-bottom: 220px;
		.download__image-center {
			@include size(640px, 376px, 12px);
		}
		.download__image-left,
		.download__image-right {
			@include size(510px, 290px, 12px);
			bottom: -10px;
		}
		.download__images {
			&::before, &::after {
				height: 490px;
			}
			&::before {
				bottom: -65%;
			}
			&::after {
				bottom: -110%;
			}
		}
	}

	@media screen and (min-width: 1500px) {
		.download__items {
			margin-top: -20px;
		}
		.download__image-center {
			@include size(790px, 526px, 12px);
		}
		.download__image-left,
		.download__image-right {
			@include size(660px, 440px, 12px);
			bottom: -34px;
		}
		.download__images {
			&::before, &::after {
				height: 640px;
			}
			&::before {
				bottom: -50%;
			}
			&::after {
				bottom: -90%;
			}
		}
	}
}