#why {
	padding-bottom: 60px;
	.why__title {
		margin-bottom: 40px;
		text-align: center;
	}
  .why-item {
    margin-bottom: 20px;
  }

  @include table {
		padding-bottom: 160px;
		.why__title {
			margin-bottom: 80px;
		}
		.why-item {
			margin-bottom: 0;
		}
    .why__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
    }
  }

	@include desktop {
		padding-bottom: 320px;
	}
}
