.howToUse-item {
  padding: 10px 30px 18px;
  position: relative;
  border-left: 1px solid rgba(70, 70, 70, 1);
  text-align: left;
  &__title {
    font-size: 14px;
    line-height: 18px;
    margin: 58px 0 16px;
  }
  &__text {
    color: $white;
    font-size: 14px;
    line-height: 18px;
    font-family: $SF_Pro_Bold, sans-serif;
  }
  &__img {
    position: relative;
    margin-left: 31px;
    margin-top: 36px;
    &::before {
      content: '';
      position: absolute;
      top: -20px;
      left: -20px;
      z-index: -1;
      @include size(86px, 86px, 50%);
      border-right: 1px dashed rgba(70, 70, 70, 1);
      background-color: $bg_secondary;
    }
    &::after {
      content: '';
      position: absolute;
      top: -31px;
      left: -31px;
      z-index: -2;
      @include size(109px, 109px, 50%);
      border-left: 1px dashed rgba(70, 70, 70, 1);
      background-color: rgba(20, 20, 20, 1);
    }
    &,
    & svg {
      width: 45px;
      height: 45px;
    }
  }

  @include table {
    &__title {
      font-size: 20px;
      line-height: 28px;
    }
    &__text {
      font-size: 20px;
      line-height: 28px;
      max-width: 280px;
      width: 100%;
    }
    &__img {
      &::before {
        @include size(101px, 101px, 50%);
      }
      &::after {
        @include size(124px, 124px, 50%);
      }
      &,
      & svg {
        width: 60px;
        height: 60px;
      }
    }
  }

  @include desktop {
    padding: 10px 60px 28px;
    &__title {
      margin: 118px 0 16px;
    }
  }
}
