html {
  position: relative;
  scroll-behavior: smooth;
}

body {
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.03em;
  font-family: $SF_Pro_Regular, sans-serif;
  color: $gray;
  background-color: $bg_primary;
  overflow-x: hidden;
  word-break: break-word;

  @include table {
    font-size: 20px;
    line-height: 28px;
  }
}

section {
  overflow: hidden;
}

ol,
ul,
li,
p,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
  color: $orange;
  font-family: $SF_Pro_Bold, sans-serif;
}

h1 {
  font-size: 34px;
  line-height: 46px;

  @include table {
    font-size: 50px;
    line-height: 62px;
  }
  @include desktop {
    font-size: 60px;
    line-height: 72px;
  }
}

h2 {
  font-size: 28px;
  line-height: 32px;
  @include table {
    font-size: 38px;
    line-height: 42px;
  }
  @include desktop {
    font-size: 48px;
    line-height: 57px;
  }
}

h4 {
  color: $white;
  font-size: 18px;
  line-height: 18px;
  @include table {
    font-size: 28px;
    line-height: 28px;
  }
}

img {
  object-fit: cover;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.container {
  max-width: $container;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.orange-text {
  color: $orange;
}

.bold-text {
  font-family: $SF_Pro_Bold, sans-serif;
}

.btn-default {
  @include flex();
  font-family: $SF_Pro_Bold, sans-serif;
  font-size: 13px;
  line-height: 17px;
  padding: 15px 23px;
  background-color: $orange;
  color: $white;
  border-radius: 12px;
  width: fit-content;
  transition: $transition;
  &:hover {
    transform: scale(1.1);
  }
  span {
    margin-left: 8px;
  }

  @include table {
    font-size: 16px;
    line-height: 20px;
    padding: 20px 28px;
  }
}

.windows-icon {
  width: 16px;
  height: 16px;
}
