#howToUse {
	padding-bottom: 40px;
	.howToUse__inner {
		text-align: center;
	}
	.howToUse__title {
		margin-bottom: 20px;
	}
	.howToUse__text {
		margin-bottom: 45px;
	}
	.howToUse-item {
		margin-bottom: 25px;
	}

  @include table {
		padding-bottom: 120px;
		.howToUse__text {
			margin-bottom: 90px;
		}
		.howToUse-item {
			margin-bottom: 0;
		}
    .howToUse__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
    }
  }

	@include desktop {
		padding-bottom: 240px;
	}
}